/* https://www.colorcombos.com/color-schemes/650/ColorCombo650.html */
/* https://flatuicolors.com/palette/defo */
* {
  margin: 0; }

.gameInfo {
  display: flex;
  width: 50%;
  justify-content: space-between; }

.gameInfo > div {
  color: #c0392b;
  font-weight: 800;
  margin-left: 5px;
  margin-right: 5px; }

.board {
  background-color: #2c3e50;
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  height: 100%; }

.fret {
  border-left: 3px;
  border-right: 3px;
  border-style: solid;
  border-image: linear-gradient(to left, white, black, white) 20;
  border-top: 0px;
  border-bottom: 0px;
  height: 50px;
  width: 50px;
  float: left; }

.wire:nth-of-type(3) > .fret:nth-of-type(4)::after, .wire:nth-of-type(3) > .fret:nth-of-type(6)::after, .wire:nth-of-type(3) > .fret:nth-of-type(8)::after, .wire:nth-of-type(3) > .fret:nth-of-type(10)::after {
  justify-content: center;
  display: flex;
  content: ' \25CF';
  color: white;
  font-size: 50px;
  position: relative;
  bottom: 10px; }

.wire:nth-of-type(2) > .fret:nth-of-type(12)::after, .wire:nth-of-type(4) > .fret:nth-of-type(12)::after {
  justify-content: center;
  left: 100%;
  display: flex;
  content: ' \25CF';
  color: white;
  font-size: 50px;
  position: relative;
  bottom: 10px; }

.wire {
  display: flex;
  flex-direction: row;
  width: auto;
  background-color: #f39c12;
  border-width: 1px;
  border-color: black;
  border-style: solid;
  height: 50px; }

.fretboard {
  display: flex;
  flex-direction: column; }

.options {
  background-color: #2c3e50;
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  color: #f39c12
 }

.tuningOptions {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  text-align: center; }

.stringNumberSelector {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3; }

.fretNumberSelector {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 3;
  grid-row-end: 4; }

.timeSelectorDiv {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 4;
  grid-row-end: 5; }

.questionFretButton {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 4; }

.noteButton {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 4;
  grid-row-end: 6; }

.game {
  display: grid;
  grid-template-columns: 3fr 3fr;
  grid-template-rows: 3fr 1fr;
  height: 100vh;
  background-color:#2c3e50 }

.hidden {
  opacity: 0.3;
  pointer-events: none; }

.questionNode {
  background-color: #c0392b; }

.selectNoteButtons {
  display: flex;
  height: 50px;
  width: 52px;
  margin-left: 3px;
  margin-right: 1px; }

.selectNoteDiv {
  display: flex;
  flex-direction: row; }

.selectNoteButtons {
  text-align: center;
  vertical-align: middle;
  display: inline;
  border-color: #c0392b;
  color: #fff;
  box-shadow: 0 0 40px 40px #c0392b inset, 0 0 0 0 #c0392b;
  transition: all 150ms ease-in-out;
  color: #CCCCCC; }

.gameStartButton {
  border-color: #c0392b;
  color: #fff;
  box-shadow: 0 0 40px 40px #c0392b inset;
  color: #CCCCCC;
  margin: 8px; }


.scores {
  background-color: #2c3e50;
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 3;
  height: 100%; }

.noteScoreBoard, .questionFretScoreBoard {
  padding: 5px;
  margin: 15px;
  border-top: 0px;
  border-bottom: 0px;
  border-left: 5px;
  border-right: 5px;
  border-style: solid;
  border-color: #c0392b;
  margin-top: 30px;
  color:#f39c12
 }

.scoreHeader {
  position: relative;
  top: -15px; }

.ranking {
  display: flex;
  flex-direction: row;
  height: 100%; }



  /* Responsive */
.portraitPrompt{
  display:none;

}
  /* Collapsing items if on mobile */

  @media((min-width:1px) and (max-width:680px)){
    /*Portrait mode prompt*/
    .portraitPrompt{
      position:absolute;
      display:block;
      top:0;
      left:0;
      height:100vh;
      width:100vw;
      background-color: rgba(0,0,0,0.6);
      display:flex;
      flex-direction:column;
      align-items:center;
      justify-content:center;
    }
    .promptText{
      color:#CCCCCC;
      font-size:20px;
    }
    .portraitPrompt.dismissedPrompt{
      display:none;
    }
  }
  @media((min-width:681px) and (max-width: 1365px)){
    .game{
      grid-template-columns: 1fr;
      grid-template-rows:3fr;
    }
    .board{
      margin-left:0px;
      grid-row-start:1;
      grid-row-end:2;
      grid-column-start:1;
      grid-column-end:2;
    }
    .options{
      grid-row-start:2;
      grid-row-end:3;
      grid-column-start:1;
      grid-column-end:2;
      margin:auto;
    }
    .scores{
      grid-row-start:3;
      grid-row-end:4;
      grid-column-start:1;
      grid-column-end:2;
      margin:auto;
    }
  }
  /* Desktop centering of fretboard*/
@media ((min-width: 1300px) and (max-width: 1366px)) {
  .board{
    margin-left:383px;
  }
}
@media ((min-width: 1367px) and (max-width: 1536px)) {
  .board{
    margin-left:468px;
  }
}
@media ((min-width: 1537px) and (max-width: 1920px)) {
  .board{
    margin-left:650px;
  }
}

